body {
  font-family: "noto_sansregular", Arial, sans-serif, -apple-system, system-ui, BlinkMacSystemFont;
  font-size: 16px;
  color: #3c3c3b;
  background: #ffffff;
  /*overflow: hidden;*/
}

h1, h2, h3, h4 {
  font-family: "noto_serifsemibold", Arial, sans-serif, -apple-system, system-ui, BlinkMacSystemFont;
}

h1 {
  font-size: 32px;
  margin-bottom: 16px;
  color: #3c3c3b;
  font-weight: 700;
  line-height: 130%;
}

h2 {
  font-size: 24px;
  margin-bottom: 16px;
  color: #3c3c3b;
  line-height: 130%;
}

h3 {
  font-size: 20px;
  margin-bottom: 16px;
  color: #54BA4C;
  line-height: normal;
  line-height: 130%;
}
h3 a {
  text-decoration: none;
}

h4 {
  font-size: 16px;
  margin-bottom: 16px;
  color: #2C7845;
}

p {
  font-size: 16px;
  margin-bottom: 16px;
  line-height: 22px;
}
p.copyright {
  font-size: 12px;
  margin-bottom: 0;
}

strong {
  font-family: "noto_sansbold";
  font-weight: 600;
}

em {
  font-style: italic;
}

ul {
  margin-left: 32px;
}
ul li {
  list-style-type: disc;
  margin-bottom: 12px;
}

ol {
  list-style-type: decimal;
  margin-left: 32px;
}
ol li {
  margin-bottom: 12px;
}

a {
  color: #2C7845;
}
a:hover {
  color: #3c3c3b;
}

.page-content ul, .menu-slide-content ul {
  list-style-type: square;
  margin-left: 32px;
  margin-bottom: 16px;
}
.page-content ul li, .menu-slide-content ul li {
  margin-bottom: 14px;
  line-height: 160%;
}

.page-content ol {
  list-style-type: numeric;
  margin-left: 32px;
  margin-bottom: 16px;
}
.page-content ol li {
  margin-bottom: 14px;
  line-height: 160%;
}

nav {
  text-align: right;
}
nav ul li {
  display: inline;
  margin-left: 16px;
  font-size: 1.1rem;
  position: relative;
  list-style-type: none;
}
nav ul li a {
  color: #3c3c3b;
  text-decoration: none;
}
nav ul li a:hover {
  color: #2C7845;
}
nav ul li.current-menu-item a {
  color: #2C7845;
}
nav ul li.current-page-ancestor a {
  color: #2C7845;
}
nav .sub-menu {
  background: #2C7845;
  display: none;
  color: #ffffff;
  padding: 16px 0 0;
  position: absolute;
  left: 0;
  width: 210px;
  z-index: 10;
  margin-left: 0;
  margin-bottom: 0;
}
nav .sub-menu li {
  display: block;
  text-align: left;
  margin-bottom: 8px;
}
nav .sub-menu li a, nav .sub-menu li .current-page-ancestor a {
  color: #ffffff !important;
}
nav .sub-menu li a:hover, nav .sub-menu li .current-page-ancestor a:hover {
  color: #ffffff;
  text-decoration: underline;
}
nav li.menu-item-has-children:hover .sub-menu {
  display: block;
}

.single-cpt_blog nav ul li.blog a {
  color: #2C7845;
}

#mobile-navbar {
  display: none;
}

.footer-nav-list {
  margin-left: 0;
}
.footer-nav-list li {
  padding-bottom: 8px;
  list-style-type: none;
  margin-bottom: inherit;
  margin-left: 0;
}
.footer-nav-list li:last-child a {
  border: none;
}
.footer-nav-list li a {
  color: #2C7845;
  text-decoration: none;
  font-size: 14px;
}
.footer-nav-list li a:hover {
  color: #3c3c3b;
  text-decoration: underline;
}

@media (max-width: 768px) {
  #menu-main {
    display: none;
  }

  nav {
    padding-right: 16px;
  }

  #mobile-navbar.navbar.mobile-open {
    display: block;
    width: 80%;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    text-align: left;
    padding: 20px;
    background: #fff;
    z-index: 30;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav {
    display: block;
    width: 100%;
    height: 100%;
    margin-left: 0;
    margin-top: 16px;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li {
    display: block;
    padding-bottom: 16px;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li a {
    color: #2C7845;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li a:hover {
    color: #3c3c3b;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.current-menu-item a {
    color: #2C7845;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li a::after {
    content: "";
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .submenu-parent {
    flex-grow: 1;
    flex-basis: 80%;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .open-submenu {
    display: inline-block;
    flex-basis: 20%;
    cursor: pointer;
    color: #2C7845;
    font-size: 120%;
    text-align: center;
    line-height: 150%;
    border-left: 1px solid #ffffff;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .open-submenu:hover {
    text-decoration: none;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .open-submenu:hover.sub-opened {
    transform: rotate(180deg);
    border: none;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .sub-menu {
    display: none;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .sub-menu.opened-menu {
    display: block;
    position: relative;
    flex-basis: 100%;
    width: 100%;
    left: 0;
    background: none;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .sub-menu.opened-menu li a {
    color: #2C7845;
  }
  #mobile-navbar.navbar.mobile-open .navbar-nav li.menu-item-has-children .sub-menu.opened-menu li a:hover {
    text-decoration: underline;
  }

  .mobile-menu-icon {
    display: block;
    cursor: pointer;
  }
  .mobile-menu-icon .bar1, .mobile-menu-icon .bar2, .mobile-menu-icon .bar3 {
    width: 35px;
    height: 2px;
    background-color: #2C7845;
    margin: 6px 0;
    transition: 0.4s;
  }
}
/* Slide in menu */
.menu-slide-container {
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0px;
  bottom: 0;
  width: 50%;
  max-width: 70em;
  height: 100%;
  background: #f1f1f1;
  padding: 32px 16px;
  overflow: auto;
  z-index: 5;
}
@media (max-width: 768px) {
  .menu-slide-container {
    width: 100%;
  }
}
.menu-slide-container.active {
  visibility: visible;
}
.menu-slide-container .menu-close {
  margin-bottom: 16px;
  text-align: right;
}
.menu-slide-container .menu-slide-content {
  max-width: 40em;
  padding-top: 32px;
  margin: 0 auto;
}
.menu-slide-container .menu-slide-content h2 {
  margin-top: 32px;
}

.lock-body {
  overflow: hidden;
}

html {
  box-sizing: content-box;
}

#overlay {
  position: fixed;
  /* Sit on top of the page content */
  display: none;
  /* Hidden by default */
  width: 100%;
  /* Full width (cover the whole page) */
  height: 100%;
  /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
  /* Black background with opacity */
  z-index: 1;
  /* Specify a stack order in case you're using a different order for other elements */
}

header {
