.wrapper {
  max-width: 1170px;
  /*display: grid;
  grid-template-columns: auto $max-site-width  auto;
  grid-template-areas: ". sitecontent .";*/
  padding: 0 16px;
  margin: 0 auto 16px;
}

.wrapper-sidebar {
  width: 100%;
  display: grid;
  grid-template-columns: auto minmax(auto, 720px) 200px minmax(auto, 250px) auto;
  grid-template-areas: ". sitecontent . sidebar .";
  padding: 0 16px;
  margin-bottom: 16px;
  /*grid-gap: 200px;*/
}
.wrapper-sidebar .page-content {
