  .wrapper-sidebar {
    grid-template-columns: auto minmax(auto, 1170px) auto;
    grid-template-areas: ". sitecontent ." ". sidebar .";
  }
}

.wrapper-full {
  width: 100%;
  display: grid;
  grid-template-columns: auto minmax(auto, 1170px) auto;
  grid-template-areas: ". content ." ". contentlower .";
  margin: 0 0 16px 0;
}

.wrapper-2col {
